import cities from './russiaSities';

export default function (val) {
    const citiArray = [];
    let index = null;

    let finded = cities.find((el, i) => {
        index = i;
        return el.city.toLocaleLowerCase().startsWith(val.toLocaleLowerCase());
    });

    if (finded) {
        citiArray.push(finded);
    }

    if (citiArray.length !== 0) {
        for (let i = 1; i < 6; i++) {
            if ((index + i) < cities.length && cities[index + i].city.toLocaleLowerCase().startsWith(val.toLocaleLowerCase())) {
                citiArray.push(cities[index + i]);
            } else {
                break;
            }
        }
    }

    return citiArray;
}