import React, { Component } from "react";
import './SelectorChooser.css';
import SelectorInput from '../SelectorInput/SelectorInput';
import CityItem from '../CityItem/CityItem';
import citySearch from "../../../citySearch";

class SelectorChooser extends Component {

    state = {
        value: '',
        searching: false,
        searchingComplete: false,
        searchComplete: true,
        findedCities: null,
        chosenCity: null
    }


    inputChangeHandler = (evt) => {
        if (!evt.target.value) {
            this.setState({searching: false, value: null, findedCities: null});
            return;
        }

        this.setState({searchComplete: false});

        if (!this.state.searching) {
            this.setState({searching: true});
        }

        const findedCities = citySearch(evt.target.value);

        if (findedCities.length > 0) {
            this.setState({value: evt.target.value, findedCities: findedCities});
        } else {
            this.setState({value: null, findedCities: null, searchComplete: true});
        }
    }

    inputonBlurHandler = () => {
        if (this.state.searchComplete) {
            this.setState({searching: false, value: null});
        }

    }

    onChoosehandler = (value) => {
        console.log(value.city, value.region);
        this.setState({searchComplete: true, searching: false, value: null});
    }

    render() {
        let citiesList = null;

        if (this.state.findedCities) {
            citiesList = this.state.findedCities.map((el, i) => {
                return <CityItem key={i} value={el} clicked={() => this.onChoosehandler(el)} />;
            })
        } else if (this.state.searching) {
            citiesList = <span className="nothing-find">Ничего не найдено.</span>;
        }

        const fieldStyle = {
            display: this.state.searching ? 'block' : 'none'
        }

        return (
            <div className="selectorChooser">
                <SelectorInput
                    inputChange={this.inputChangeHandler}
                    ifBlur={this.inputonBlurHandler}
                />
                <ul className="selectorChooser__choose-field" style={fieldStyle}>
                    {citiesList}
                </ul>
            </div>

        );
    }
}

export default SelectorChooser;